.game-tabs {
  background-color: var(--xb-black);
  color: var(--xb-white);
}

.bg-xb-green {
  background-color: var(--xb-green);
}

.gp-fade-in {
  animation: gpFadeIn ease 2s;
}

.mh-68 {
  min-height: 68vh;
}

.nav-link {
  color: var(--xb-green) !important;
}

@keyframes gpFadeIn{
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}