.grid-item {
  outline: auto;
  outline-color: var(--xb-green-light);
  width: 100%;
  background-color: var(--xb-green-dark);
}

.img-resp {
  width: 100%;
  height: auto;
}

.placeholder {
  width: 100%;
  height: auto;
  min-height: 130px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url('https://upload.wikimedia.org/wikipedia/commons/c/c2/No_image_poster.png');
}

.image-text {
  background-color: var(--xb-green-dark);
  color: var(--xb-white);
}

.steamDb {
  background-color: var(--xb-green-light);
  color: var(--xb-white);
}

.scores, .station {
  width: 100%;
}

.icon-green {
  color: var(--xb-green-dark);
}

@media (width < 576px) {
  .item {
    min-width: 100px;
  }
  .image-text {
    font-size:x-small
  }
}

