.flex-container {
  display: flex;
  flex-flow: column;
  height: 100%;
}

.flex-content {
  flex: 1 1 auto;
}

.xgp-bg-img {
  background-image: url('../images/xgp-bg-img.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.fade-in {
  animation: fadeIn ease 5s;
}

.hyperlink {
  text-decoration: none;
  color: white;
}

@keyframes fadeIn{
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

