.paginator {
  background-color: var(--xb-black) !important;;
}

.page-link {
  background-color: var(--xb-green-dark) !important;
  color: var(--xb-white) !important;
  border: 1px solid var(--xb-green) !important;
}

.active > .page-link {
  border-color: var(--xb-white) !important;
  background-color: var(--xb-white) !important;
  color: var(--xb-black) !important;
}